import { Component } from '@angular/core';
import { ModalController, NavController } from '@ionic/angular';
import { ModalPage } from '../modal/modal.page';
import { ClaimPage } from '../claim/claim.page';

@Component({
  selector: 'app-home',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss'],
})
export class HomePage {
  clientFirstName: string = "Simon"
  institutionName: string = "CSSS De La Montagne";
  groupName: string = "Groupe 123456";
  certificateName: string = "Certificat 983727272";
  modal: HTMLIonModalElement = null;

  notifications: any[] = [{ 
    text: "Désirez-vous faire une réclamation pour votre visite au Chiropraticien l’Institut?",
    route: "/claim"
  }];

  constructor(public modalController: ModalController, public navController: NavController) {}

  async showNotifications() {
    this.modal = await this.modalController.create({
      component: ModalPage,
      componentProps: {
        'notifications': this.notifications
      }
    });
    
    return await this.modal.present();
  }

  async goToClaimPage(){
    
    await this.navController.navigateForward("/claim");
  }
}
